'use strict';

var utils = require('./utils');
var bind = require('./helpers/bind');
var axiosConfig = require('./core/axiosConfig');
var mergeConfig = require('./core/mergeConfig');
var defaults = require('./defaults');

/**
 * Create an instance of axiosConfig
 *
 * @param {Object} defaultConfig The default config for the instance
 * @return {axiosConfig} A new instance of axiosConfig
 */
function createInstance(defaultConfig) {
  var context = new axiosConfig(defaultConfig);
  var instance = bind(axiosConfig.prototype.request, context);

  // Copy axiosConfig.prototype to instance
  utils.extend(instance, axiosConfig.prototype, context);

  // Copy context to instance
  utils.extend(instance, context);

  // Factory for creating new instances
  instance.create = function create(instanceConfig) {
    return createInstance(mergeConfig(defaultConfig, instanceConfig));
  };

  return instance;
}

// Create the default instance to be exported
var axiosConfig = createInstance(defaults);

// Expose axiosConfig class to allow class inheritance
axiosConfig.axiosConfig = axiosConfig;

// Expose Cancel & CancelToken
axiosConfig.Cancel = require('./cancel/Cancel');
axiosConfig.CancelToken = require('./cancel/CancelToken');
axiosConfig.isCancel = require('./cancel/isCancel');
axiosConfig.VERSION = require('./env/data').version;

// Expose all/spread
axiosConfig.all = function all(promises) {
  return Promise.all(promises);
};
axiosConfig.spread = require('./helpers/spread');

// Expose isaxiosConfigError
axiosConfig.isaxiosConfigError = require('./helpers/isaxiosConfigError');

module.exports = axiosConfig;

// Allow use of default import syntax in TypeScript
module.exports.default = axiosConfig;
