'use strict';

var utils = require('./../utils');

/**
 * Determines whether the payload is an error thrown by axiosConfig
 *
 * @param {*} payload The value to test
 * @returns {boolean} True if the payload is an error thrown by axiosConfig, otherwise false
 */
module.exports = function isaxiosConfigError(payload) {
  return utils.isObject(payload) && (payload.isaxiosConfigError === true);
};
